<template>
  <div class="payment" v-if="product">
    <div class="allAddress">
      <div class="address" v-if="product.addressInfo == null">
        <van-cell is-link title="设置收货地址" @click.stop="show = true" />
        <van-action-sheet v-model="show" title="选择地址">
          <div class="content">
            <img src="../assets/dizi.png" alt="" />
            <van-button round class="signout" to="/address" type="info"
              >新加地址</van-button
            >
          </div>
        </van-action-sheet>
      </div>
      <div class="address" v-if="product.addressInfo != null">
        <van-cell is-link @click.stop="show = true">
          <template #title>
            <div>
              <span class="custom-title titleson">{{
                product.addressInfo.real_name
              }}</span>
              <span class="custom-title titleson2">{{
                product.addressInfo.phone
              }}</span>
            </div>
            <div>
              {{ product.addressInfo.province }}{{ product.addressInfo.city
              }}{{ product.addressInfo.district }}
            </div>
          </template>
        </van-cell>
        <van-action-sheet v-model="show" title="选择地址">
          <div class="content">
            <div
              class="list"
              v-for="add in address"
              :key="add.id"
              @click="adddefault(add.id)"
              :style="{ color: add.is_default == 1 ? '#fc4141' : '' }"
            >
              <div class="item1">
                <div class="addressTxt"><van-icon name="location" /></div>
                <div class="addressTxt1">
                  <div class="box">
                    <span class="custom-title titleson">{{
                      add.real_name
                    }}</span>
                    <span class="custom-title titleson2">{{ add.phone }}</span>
                  </div>
                  <div class="box1">
                    {{ add.province }}{{ add.city }}{{ add.district }}
                  </div>
                </div>
              </div>
              <div class="icon"><van-icon name="certificate" /></div>
            </div>
            <van-button round class="signout" to="/address" type="info"
              >新加地址</van-button
            >
          </div>
        </van-action-sheet>
      </div>
      <div class="line">
        <img src="../assets/line.jpg" alt="" />
      </div>
    </div>
    <div class="orderGoods">共{{ product.cartInfo.length }}件商品</div>
    <div v-if="product">
      <div
        class="commodity"
        v-for="cartIn in product.cartInfo"
        :key="cartIn.id"
      >
        <div class="commodityson1">
          <div class="pictrue">
            <img :src="cartIn.productInfo.image" alt="" />
          </div>
          <div class="text">
            <div class="name">
              <span>
              {{ cartIn.productInfo.store_name }}
              </span>
              <span>x&nbsp;{{cartIn.cart_num}}</span>
            </div>
            <div class="money">
              <div>{{ cartIn.productInfo.attrInfo.suk }}</div>
              <div>￥{{ cartIn.productInfo.price }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="list">
      <div class="item">
        <div>积分抵扣</div>
        <div class="text">
          当前积分<span class="textson">{{ product.userInfo.integral }}</span>
          <van-checkbox v-model="checked" @change="Change"></van-checkbox>
        </div>
      </div>
      <div class="item">
        <div>会员优惠</div>
        <div class="text">-￥{{ product.cartInfo[0].vip_truePrice }}</div>
      </div>
      <div class="item">
        <div>快递费用</div>
        <div class="text">免运费</div>
      </div>
      <div class="item1">
        <div>备注信息</div>
        <textarea
          name=""
          id=""
          cols="30"
          rows="10"
          placeholder="请添加备注（150字以内）"
        >
        </textarea>
      </div>
    </div>
    <div class="item2">
      <div>支付方式</div>
      <div class="wechatitem" @click="indexcolor=1" 
      :style="{borderColor: indexcolor == 1 ?'#fc5445':'',
      color:indexcolor == 1 ?'#e93323':''
      }">
        <div><van-icon name="wechat" />&nbsp;微信支付</div>
        <div>微信快捷支付</div>
      </div>
      <div class="wechatitem2" @click="indexcolor=2" 
      :style="{borderColor: indexcolor == 2 ?'red':'',
      color:indexcolor == 2 ?'#e93323':''}">
        <div><van-icon name="gold-coin" />&nbsp;余额支付</div>
        <div>可用余额：{{ product.userInfo.now_money }}</div>
      </div>
    </div>
    <div class="moneyList">
      <div>
        <span>商品总价：</span>
        <span>￥{{price/100}}</span>
      </div>
      <div v-if="product.priceGroup.storePostage">
        <span>运费：</span>
        <span>+￥{{product.priceGroup.storePostage}}</span>
      </div>
    </div>
    <van-submit-bar :price="price" button-text="立即结算" @submit="onSubmit" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      product: null,
      checked: false, //积分是否选中
      checkeds: false,
      show: false, //地址栏滑入滑出
      indexcolor:1,
      price: 0,
      priceing:0,
      priceend: null,
      address: [],
    };
  },
  created() {
    this.$axios
      .post("/api/order/confirm", {
        cartId: this.$route.query.id,
      })
      .then((d) => {
        if (d.data.status == 200) {
          this.product = d.data.data;
          console.log(this.product);
          for (let i = 0; i <= this.product.cartInfo.length; i++) {
            this.price +=
              (this.product.cartInfo[i].productInfo.price * this.product.cartInfo[i].cart_num - 
                this.product.cartInfo[i].vip_truePrice * this.product.cartInfo[i].cart_num) *
              100;
          }
        } else {
          this.$toast(d.data.msg);
        }
      });
    //地址列表
    this.$axios.get("/api/address/list?page=1&limit=20").then((d) => {
      this.address = d.data.data;
    });
  },
  methods: {
    //选择地址
    adddefault(event) {
      this.product.addressInfo.id = event;
      this.$axios
        .post("/api/address/default/set", {
          id: this.product.addressInfo.id,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast({
              message: "添加地址成功",
            });
          } else {
            this.$toast(d.data.msg);
          }
        });
      //再次请求地址数据
      this.$axios
        .post("/api/order/confirm", {
          cartId: this.$route.query.id,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.product = d.data.data;
            console.log(this.product.orderKey);
            for (let i = 0; i <= this.product.cartInfo.length; i++) {
              this.price +=
                (this.product.cartInfo[i].productInfo.price -
                  this.product.cartInfo[i].vip_truePrice) *
                100;
            }
          } else {
            this.$toast(d.data.msg);
          }
        });
      this.$axios.get("/api/address/list?page=1&limit=20").then((d) => {
        this.address = d.data.data;
      });
      this.show = false;
    },
    //计算价格
    Change(checked) {
      if (checked == true) {
        this.priceing = this.price / 100;
        this.priceend = this.priceing * 100;
        this.priceing = this.priceing - this.product.userInfo.integral;
        this.price = this.priceing
        if (this.priceing < 0) {
          this.price = 0;
        }
      } else if (checked == false) {
        this.priceing = this.priceend;
        this.price = this.priceing
      }
      console.log(this.product.addressInfo.id);
    },
    //提交订单
    onSubmit() {
      if (this.product.addressInfo == null) {
        this.$toast("请选择收货地址");
        return;
      }
      // this.$axios
      // .post("/api/order/computed/" + this.product.orderKey, {
      //   addressId: this.product.addressInfo.id,
      //   couponId: 0,
      //   payType: "yue",
      //   shipping_type: 1,
      //   useIntegral: 0,
      // })
      // .then((d) => {
      //   if (d.data.status == 200) {
      //     console.log(this.product);
      //   } else {
      //     this.$toast(d.data.msg);
      //   }
      // });
      //提交订单接口
      this.$axios
        .post("/api/order/create/" + this.product.orderKey, {
          addressId: this.product.addressInfo.id,
          bargainId: 0,
          combinationId: 0,
          couponId: 0,
          from: "weixinh5",
          mark: "",
          payType: "yue",
          phone: "",
          pinkId: 0,
          real_name: "",
          seckill_id: 0,
          shipping_type: 1,
          store_id: 0,
          useIntegral: 0,
        })
        .then((d) => {
          if (d.data.status == 200) {
            console.log(this.product);
          } else {
            this.$toast(d.data.msg);
          }
        });
      this.$router.push({ name: "Order", params: { type: 1 } });
    },
  },
};
</script>
<style lang="less" scoped>
.payment {
  min-height: 100vh;
  background-color: rgb(245, 245, 245);
  .allAddress {
    width: 100%;
    background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#e93323),
      to(#f5f5f5)
    );
    background-image: linear-gradient(180deg, #e93323 0, #f5f5f5);
    padding-top: 10px;
    .address {
      height: 47px;
      margin: 0 10px;
      padding: 14px 0px;
      background-color: white;
      border-radius: 5px;
      .van-cell {
        padding: 0 15px;
        align-items: center;
      }
      .van-cell__title {
        -webkit-flex: 7;
        .titleson {
          font-size: 15px;
          font-weight: 700;
          margin-right: 10px;
        }
        .titleson2 {
          font-size: 12px;
        }
      }
      .content {
        height: 248px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        img {
          width: 207px;
          height: 168px;
        }
        .list {
          width: 100%;
          padding: 0 15px;
          box-sizing: border-box;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .item1 {
            width: 80%;
            display: flex;
            align-items: center;
            .addressTxt {
            }
            .addressTxt1 {
              .box {
                text-align: left;
                .titleson2 {
                  margin-left: 8px;
                }
              }
              .box1 {
                text-align: left;
              }
              margin-left: 10px;
            }
          }
          .icon {
            font-size: 24px;
          }
        }
        .signout {
          width: 345px;
          height: 45px;
          margin: 38px 15px 0 15px;
          background-color: #e93323;
          border: none;
        }
      }
    }
    .line {
      width: 100%;
      padding: 0 10px;
      box-sizing: border-box;
      img {
        width: 100%;
        height: 1.5px;
      }
    }
  }
  .orderGoods {
    background-color: #fff;
    padding: 0 15px;
    line-height: 43px;
    font-size: 15px;
  }
  .commodity {
    background-color: #fff;
    height: 90px;
    height: 90px;
    display: flex;
    align-items: center;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    .commodityson1 {
      width: 100%;
      padding: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 15px;
      .pictrue {
        width: 65px;
        height: 65px;
        padding: 0;
        img {
          width: 65px;
          height: 65px;
          border-radius: 3px;
        }
      }
      .text {
        width: 268.5px;
        height: 64px;
        font-size: 14px;
        padding: 0;
        .name {
          padding: 0;
          height: 19px;
          width: 267.5px;
          display: flex;
          justify-content: space-between;
          span{
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          &:first-child{
            width: 226px;
          }
          &:last-child{
            line-height: 19px;
            font-size: 13px;
            color: #868686;
          }
          }
        }
        .money {
          padding: 0;
          height: 38px;
          width: 57px;
          color: #999;
          font-size: 12px;
          div {
            padding: 0;
            padding: 3.5px 0 0 0;
            &:nth-child(2) {
              font-size: 13px;
              color: #fc4141;
              padding: 8.5px 0 0 0;
            }
          }
        }
      }
    }
  }
  .list {
    background-color: #fff;
    font-size: 15px;
    .item {
      display: flex;
      padding: 15px;
      border-bottom: 1px solid #eee;
      align-items: center;
      justify-content: space-between;
      .text {
        display: flex;
        color: #868686;
        .textson {
          color: #fc4141;
          display: flex;
          align-items: center;
          margin: 0 10px 0 3px;
        }
      }
    }
    .item1 {
      padding: 15px;
      border-bottom: 1px solid #eee;
      textarea {
        background-color: #f9f9f9;
        outline: none;
        border: 0;
        -webkit-appearance: none;
        border-radius: 0;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        width: 344px;
        height: 70px;
        border-radius: 0.03rem;
        margin-top: 0.3rem;
        padding: 0.25rem 0.28rem;
        overflow: auto;
      }
    }
  }
  .item2 {
    padding: 15px;
    font-size: 15px;
    background-color: #fff;
    margin-top: 6.5px;
    border-bottom: 1px solid #eee;
    .wechatitem {
      margin-top: 18px;
      border: 1px solid #eee;
      width: 345px;
      font-size: 14px;
      line-height: 43px;
      display: flex;
      div {
        width: 50%;
        text-align: center;
        &:first-child {
          i {
            width: 22px;
            line-height: 22px;
            text-align: center;
            margin-right: 5px;
            background-color: #41b035;
            font-size: 15px;
            color: #fff;
            border-radius: 50%;
          }
        }
        &:last-child {
          color: #aaa;
          border-left: 1px solid #eee;
        }
      }
    }
    .wechatitem2 {
      font-size: 14px;
      margin-top: 10px;
      border: 1px solid #eee;
      width: 345px;
      line-height: 43px;
      display: flex;
      div {
        width: 50%;
        text-align: center;
        &:first-child{
           i {
            width: 22px;
            line-height: 22px;
            text-align: center;
            margin-right: 5px;
            font-size: 24px;
            color: #fe960f;
            border-radius: 50%;
          }
        }
        &:last-child {
          color: #aaa;
          border-left: 1px solid #eee;
        }
      }
    }
  }
  .moneyList{
    padding: 15px;
    font-size: 14px;
    background-color: #fff;
    margin-top: 6.5px;
    border-bottom: 1px solid #eee;
    div{
      display: flex;
      justify-content: space-between;
      span{
        &:last-child{
              color: #868686;
        }
      }
      &:last-child{
        margin-top: 10px;
      }
    }
  }
}
</style>